
  
  .app {
    height: 70vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: "Montserrat", sans-serif;
  }
  
  .time-select,
  .sound-picker {
    height: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    flex: 1;
  }
  
  .time-select button,
  .sound-picker button {
    color: white;
    width: 30%;
    height: 10%;
    background: none;
    font-size: 20px;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid white;
    transition: all 0.5s ease;
  }
  
  .sound-picker button {
    border: none;
    height: 120px;
    width: 120px;
    padding: 30px;
    border-radius: 50%;
  }
  
  .sound-picker button:nth-child(1) {
    background: #3a7cc7;
  }
  
  .sound-picker button:nth-child(2) {
    background: #e7bd48;
  }
  
  .sound-picker button img {
    height: 100%;
  }
  
  .time-select button:hover {
    background: white;
    color: black;
  }
  
  .player-container {
    position: relative;
    height: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    flex: 1;
  }
  
  .player-container svg {
    position: absolute;
    height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-90deg);
    pointer-events: none;
  }
  
  .player-container svg circle {
    transition: all 0.2s ease-in-out;
  }
  
  .time-display {
    color: white;
    position: absolute;
    font-size: 50px;
    bottom: 5%;
  }
  
  video {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    z-index: -10;
  }
  
  .play {
    cursor: pointer;
    margin-top: 120px;
  }
  .replay {
    cursor: pointer;
    padding-top: 150px;
  }
  h1 {
    text-align: center;
    color: white;
    padding-top: 20px;
  }